import { combineReducers, createStore } from "redux";
import productsListReducer from "./productListReducer";
import cardItemsReducer, {
  cartAddItem,
  cartRemoveItem,
  decreaseItemQuantity,
  increaseItemQuantity,
} from "./cartItemsReducer";
import wishListReducer, {
  addWishListItem,
  removeWishListItem,
} from "./wishListReducer";

const reducer = combineReducers({
  products: productsListReducer,
  cartsList: cardItemsReducer,
  wishList: wishListReducer,
});

console.log(reducer);
const store = createStore(reducer, window.__REDUX_DEVTOOLS_EXTENSION__?.());
console.log(store);

store.dispatch(cartAddItem(1, 1));
store.dispatch(cartAddItem(12, 1));
store.dispatch(cartAddItem(6, 2));
store.dispatch(cartAddItem(6, 1));
store.dispatch(cartRemoveItem(6));
store.dispatch(increaseItemQuantity(12));
store.dispatch(increaseItemQuantity(1));
store.dispatch(increaseItemQuantity(1));
store.dispatch(decreaseItemQuantity(12));

store.dispatch(addWishListItem(15));
store.dispatch(addWishListItem(16));
store.dispatch(addWishListItem(18));
store.dispatch(removeWishListItem(18));
